<template>
  <div>
    <div class="p3 flex-row">
      <a class="tertiary font-title" @click="$router.back()"><i class="fas fa-chevron-left m1-right"></i>BACK</a>
      <div class="flex-one"></div>
      <div class="p1-bottom p1-left border-bottom-secondary">{{client.name}}</div>
    </div>

    <div class="p3 p1-top">
      <button class="button alt h-fill m3-bottom" @click="createAppointmentRequest"><i class="fas fa-plus-circle m1-right"></i>ADD SHOWING</button>
      <div class="m5-bottom">
        <title-label title="REQUESTED"></title-label>
        <appointment v-for="(appointment, i) in pending" :key="i" :appointment="appointment" mode="realtor" class="m2-bottom"></appointment>
        <p v-if="pending.length === 0" class="tertiary">No pending showings</p>
      </div>

      <div v-if="declined.length > 0" class="m5-bottom">
        <title-label title="DECLINED"></title-label>
        <appointment v-for="(appointment, i) in declined" :key="i" :appointment="appointment" mode="realtor" class="m2-bottom"></appointment>
      </div>

      <div class="m5-bottom">
        <title-label title="SCHEDULED"></title-label>
        <appointment v-for="(appointment, i) in scheduled" :key="i" :appointment="appointment" mode="realtor" class="m2-bottom"></appointment>
        <p v-if="scheduled.length === 0" class="tertiary">No scheduled showings</p>
      </div>

      <div v-if="completed.length > 0" class="m5-bottom">
        <title-label title="COMPLETED"></title-label>
        <appointment v-for="(appointment, i) in completed" :key="i" :appointment="appointment" mode="realtor" class="m2-bottom"></appointment>
      </div>

      <div class="p4-top p4-bottom border-top-tertiary">
        <div class="text-right" @click="$router.push('/disclosures')">
          <button class="button alt bg-clear border-primary contrast rounded3">VIEW DISCLOSURE<i class="fas fa-file-download m2-left"></i></button>
        </div>
      </div>
    </div>

    <transition name="slide-up">
      <div v-if="prompt" class="prompt fixed bg-base z2 shadow p3 flex-column">
        <div class="m4-bottom text-right">
          <a class="tertiary font-title" @click="prompt = false"><i class="fas fa-times m1-right"></i>CANCEL</a>
        </div>

        <title-label title="REQUEST SHOWING"></title-label>
        <form class="flex-one flex-column" @submit.prevent="requestShowing">
          <label for="startDate" class="tertiary font-title font-2 m1-bottom">DATE</label>
          <input class="input m2-bottom" name="startDate">

          <label for="startTime" class="tertiary font-title font-2 m1-bottom">TIME</label>
          <input class="input m2-bottom" name="startTime">

          <div class="flex-one"></div>
          <button class="bg-clear contrast rounded3 p2 h-fill border-primary">REQUEST</button>
        </form>
      </div>
    </transition>
  </div>
</template>

<script>
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'
import { mapGetters } from 'vuex'

export default {
  name: 'RealtorClient',
  data () {
    return {
      prompt: false,
      pending: [],
      declined: [],
      scheduled: [],
      completed: []
    }
  },
  props: ['clientId'],
  computed: {
    ...mapGetters(['clients']),
    client () {
      return this.clients.find(c => c.id === parseInt(this.clientId))
    }
  },
  components: { TitleLabel, Appointment },
  methods: {
    createAppointmentRequest () {
      this.prompt = true
    },
    requestShowing () {
      this.prompt = false
    }
  },
  inject: ['userClient'],
  mounted () {
    const self = this
    this.userClient.getAppointments(this.clientId)
      .then(appointments => {
        self.pending = appointments.pending
        self.declined = appointments.declined
        self.scheduled = appointments.scheduled
        self.completed = appointments.completed
      })
  }
}
</script>
