<template>
  <div class="appointment flex-column border-base2 rounded">
    <div class="p1 font-title text-center bg-base2">{{month}} {{day}}</div>

    <div class="flex-row align-center gradient-column">
      <div class="flex-one v-fill">
        <div v-if="showDecline" class="flex-column decline-button text-center" @click="declineAppointment">
          <div class="m1-bottom"><i class="fas fa-times"></i></div>
          <p class="font-5">DECLINE</p>
        </div>

        <div v-if="showCancel" class="flex-column decline-button text-center" @click="cancelAppointment">
          <div class="m1-bottom"><i class="fas fa-times"></i></div>
          <p class="font-5">CANCEL</p>
        </div>
      </div>

      <div class="flex-two flex-column align-center">
        <div class="flex-one font2 p1">{{displayTime}}</div>
        <div class="font-3 tertiary p1-bottom">{{dayOfWeek}}</div>
      </div>

      <div class="flex-one">
        <div v-if="showAccept" class="flex-column secondary text-center" @click="acceptAppointment">
          <div class="m1-bottom"><i class="fas fa-check"></i></div>
          <p class="font-5">ACCEPT</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Appointment',
  props: ['appointment', 'mode'],
  computed: {
    date () {
      return new Date(this.appointment.date)
    },
    month () {
      return this.time.getShortMonth(this.appointment.date).toUpperCase()
    },
    day () {
      return this.time.getDayOfMonth(this.appointment.date)
    },
    dayOfWeek () {
      return this.time.getDayOfWeek(this.appointment.date).toUpperCase()
    },
    displayTime () {
      return this.time.formatTime(this.appointment.time)
    },
    showDecline () {
      return this.mode !== 'realtor' && this.appointment.status === 'pending'
    },
    showCancel () {
      if (this.mode === 'realtor') {
        return ['pending', 'scheduled'].includes(this.appointment.status)
      } else {
        return this.appointment.status === 'scheduled'
      }
    },
    showAccept () {
      return this.mode !== 'realtor' && this.appointment.status === 'pending'
    }
  },
  inject: ['time', 'userClient'],
  methods: {
    acceptAppointment () {
      let updatedAppointment = { ...this.appointment }
      updatedAppointment.status = 'scheduled'
      this.userClient.updateAppointment(updatedAppointment)
    },
    declineAppointment () {
      let updatedAppointment = { ...this.appointment }
      updatedAppointment.status = 'declined'
      this.userClient.updateAppointment(updatedAppointment)
    },
    cancelAppointment () {
      let updatedAppointment = { ...this.appointment }
      updatedAppointment.status = 'canceled'
      this.userClient.updateAppointment(updatedAppointment)
    }
  }
}
</script>

<style lang="scss" scoped>
.appointment {
  overflow: hidden;
  .accept-button {
    button { color: green; }
    color: green;
  }
  .decline-button {
    button { color: red; }
    color: red;
  }
}
</style>
